html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.7rem;
  font-family: "Poppins", sans-serif;
}

a {
  font-size: 1.9rem;
  text-decoration: none;
  line-height: 1.6;
}

img {
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  padding: 0;
  list-style: none;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

h1 {
  margin: 2rem 0;
  line-height: 1.5;
}

h2 {
  margin: 2rem 0;
  line-height: 1.5;
}

h3 {
  margin: 2rem 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4.4rem;
  }
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: #111;
  line-height: 2;
  margin: 1rem 0;
}

.header {
  padding: 5rem 0;
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../assets/img/header.jpg");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}
.header__contenido {
  padding: 10rem 0;
}
.header__titulo {
  color: #fff;
}
@media (min-width: 768px) {
  .header__titulo {
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .header__titulo {
    max-width: 40%;
  }
}

@media (min-width: 768px) {
  .create__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
    justify-content: center;
  }
}
.create__titulo {
  display: inline;
  color: #313c4e;
  background-image: linear-gradient(to bottom, transparent 40%, #84d9bc 50%);
  background-repeat: repeat-x;
  background-size: 100% 2rem;
  background-position: 0 70%;
}

.benefists {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .benefists__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
    justify-content: center;
  }
}
.benefists__contenido {
  grid-column: 1/4;
}
.benefists__titulo {
  display: inline;
  color: #313c4e;
  background-image: linear-gradient(to bottom, transparent 40%, #fff 50%);
  background-repeat: repeat-x;
  background-size: 100% 2rem;
  background-position: 0 70%;
}
.benefists__picture {
  grid-column: 4/6;
  transition: transform 0.2s ease-in-out;
}
@media (hover: hover) {
  .benefists__picture:hover {
    transform: rotate(8deg) scale(1.1);
  }
}

.statistics__titulo {
  display: inline;
  color: #313c4e;
  background-image: linear-gradient(to bottom, transparent 40%, #84d9bc 50%);
  background-repeat: repeat-x;
  background-size: 100% 2rem;
  background-position: 0 70%;
}

.plans {
  background-color: #313c4e;
}
.plans__titulo {
  display: inline;
  color: #84d9bc;
  background-image: linear-gradient(to bottom, transparent 40%, #fff 50%);
  background-repeat: repeat-x;
  background-size: 100% 2rem;
  background-position: 0 70%;
}
.plans__paragraph {
  color: #fff;
}
.plans__cards {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .plans__cards {
    flex-direction: row;
    margin-inline: auto;
    margin-block-start: 10rem;
    max-width: 80rem;
  }
}

@media (min-width: 768px) {
  .testimonials {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
}
.testimonials__picture {
  grid-column: 1/3;
}
.testimonials__contenido {
  grid-column: 3/6;
}
.testimonials__titulo {
  display: inline;
  color: #313c4e;
  background-image: linear-gradient(to bottom, transparent 40%, #84d9bc 50%);
  background-repeat: repeat-x;
  background-size: 100% 2rem;
  background-position: 0 70%;
}

.footer {
  background-color: #313c4e;
  color: #fff;
  border-top: 1rem solid #84d9bc;
}
.footer__grid {
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}
.footer__widget {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__widget {
    margin-bottom: 0;
  }
}
.footer .logo__img {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .footer .logo__img {
    margin-bottom: 0;
  }
}
.footer__titulo {
  margin-top: 0;
  text-transform: uppercase;
  color: #84d9bc;
}
.footer__copyright {
  margin: 0 auto;
  padding: 1rem;
  max-width: 90%;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  border-top: 0.3rem solid #84d9bc;
}
@media (min-width: 768px) {
  .footer__copyright {
    max-width: 70%;
  }
}

.logo__img {
  display: block;
  margin: 0 auto;
  max-width: 20rem;
}
@media (min-width: 768px) {
  .logo__img {
    margin: 0;
  }
}

[class$=__btn], .plan-card__btn {
  display: block;
  padding: 1rem 2rem;
  background-color: #84d9bc;
  font-size: 2rem;
  font-weight: 700;
  color: #313c4e;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  [class$=__btn], .plan-card__btn {
    display: inline-block;
    width: auto;
  }
}
@media (hover: hover) {
  [class$=__btn]:hover, .plan-card__btn:hover {
    background-color: #5dcda7;
    cursor: pointer;
  }
}

.plan-card__btn {
  display: block;
}
.plan-card__btn--negocio {
  border: 2px solid #fff;
}
@media (hover: hover) {
  .plan-card__btn--negocio:hover {
    background-color: #fff;
    color: #84d9bc;
  }
}

.plan-card {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  background-color: #fff;
  border-bottom: 1px solid #e1e1e1;
  transition: transform 0.2s ease-in-out;
}
@media (min-width: 768px) {
  .plan-card {
    border-bottom: none;
  }
}
@media (hover: hover) {
  .plan-card:hover {
    transform: scale(1.1);
  }
}
.plan-card__name {
  color: #313c4e;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}
.plan-card__price {
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  margin: 1rem 0;
}
.plan-card__list-item {
  text-align: center;
  margin-bottom: 2rem;
}
.plan-card__list-item:last-of-type {
  margin-bottom: 0;
}
.plan-card--inicio {
  background-image: url("../assets/img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.plan-card--negocio {
  order: -1;
  background-color: #84d9bc;
  background-image: url("../assets/img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .plan-card--negocio {
    order: unset;
    margin: -5rem 0;
    padding-top: 21rem;
    background-position: 50% 7rem;
    border-radius: 2rem;
    z-index: 1;
  }
}
.plan-card--pro {
  background-image: url("../assets/img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}

.testimonial-card {
  margin-bottom: 2rem;
  padding: 2rem;
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimonial-card {
    max-width: 70%;
  }
  .testimonial-card:nth-child(2) {
    margin-left: 10%;
  }
  .testimonial-card:nth-child(3) {
    margin-left: 20%;
  }
  .testimonial-card:nth-child(4) {
    margin-left: 30%;
  }
}
.testimonial-card::before {
  position: absolute;
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../assets/img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center center;
}
.testimonial-card:first-of-type {
  margin-top: 2rem;
}
.testimonial-card:last-of-type {
  margin-bottom: 0;
}
.testimonial-card__paragraph {
  margin: 0;
  padding-left: 6rem;
}

.nav__link {
  display: block;
  color: #fff;
  margin: 1rem 0;
  cursor: pointer;
}
.nav__link:last-of-type {
  margin-bottom: 0;
}